*,
*:before,
*:after {
  box-sizing: border-box;
  outline: unset;
}

/* ios */
html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer,
header,
nav,
section,
main {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

a {
  color: unset;
  text-decoration: none;
}

img {
  width: 100%;
  object-fit: contain;
}

* {
  --bg-main: #171F29;
  --bg-first: #fff;
  --bg-second: #ECDFD1;
  --c-first: #47576C;
  --c-second: #171F29;
  --c-third: #192D47;
  --c-fourth: #A3ABB5;
  --c-fifth: #989899;
  --c-white: #fff;
  --c-hover-first: #171F29;
  --c-hover-second: #304259;
  --c-hover-third: #192D470D;
  --b-radius-first: 12px;
  --b-radius-second: 8px;
  --b-radius-third: 4px;
  --shadow-first: 0px 8px 24px 0px #02031CCC;
  --anim-offset: 2px;
  --anim4: 0.4s ease-in-out;
  --anim2: 0.2s ease-in-out;
  --anim1: 0.1s ease;
}

html {
  font-family: "Mulish", sans-serif;
  font-size: 16px;
  color: var(--c-first);
  cursor: default;
}

body {
  line-height: 1.5;
}

h1,
.h1 {
  font-weight: 900;
  font-size: 64px;
  line-height: 1.13;
}

h2 {
  font-weight: 700;
  font-size: 56px;
  line-height: 1.15;
}

h3,
.h3 {
  font-weight: 700;
  font-size: 48px;
  line-height: 1.17;
}

h4 {
  font-weight: 900;
  font-size: clamp(24px, 5vw, 40px);
  line-height: 1.2;
}

h5,
.h5 {
  font-weight: 900;
  font-size: 24px;
  line-height: 1.34;
}

h6,
.h6 {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
}

strong,
b {
  font-weight: 700;
}

.title--second {
  font-size: clamp(48px, 8vw, 64px);
  line-height: 1.17;
  font-weight: 900;
}
@media screen and (max-width: 820px) {
  .title--second {
    font-weight: 700;
  }
}
.title--third {
  font-weight: 900;
  font-size: clamp(24px, 5vw, 40px);
  line-height: 1.2;
}

