}
.bg {
  color: var(--c-first);
}
.bg h1,
.bg .h1,
.bg h2,
.bg .h2,
.bg h3,
.bg .h3,
.bg h4,
.bg .h4,
.bg h5,
.bg .h5,
.bg h6,
.bg .h6 {
  color: var(--c-second);
}

section ul li + li,
section ol li + li {
  margin-top: 8px;
}

p + p {
  margin-top: 10px;
}

input {
  border: 1px solid var(--c-fourth);
  border-radius: 2px;
  padding: 13px 16px;
  font-family: "Mulish", sans-serif;
  font-size: 16px;
  width: 100%;
}

button {
  cursor: pointer;
}

header .container,
footer .container,
section .container {
  border-radius: var(--b-radius-first);
}

section {
  color: var(--c-white);
}
section:first-child {
  --gap: 24px;
  margin-top: calc(var(--offset-header) + var(--gap));
}
@media screen and (max-width: 820px) {
  section:first-child {
    --gap: 16px;
  }
}

.no-scroll {
  overflow-y: hidden;
}

.footer--first .container,
.bg--first .container {
  background: var(--bg-first);
}
.footer--second .container,
.bg--second .container {
  background: var(--bg-second);
}

.row {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}

.btn {
  padding: 12px 16px;
  white-space: nowrap;
}
.btn-slider {
  width: clamp(24px, 6vw, 38px);
  height: clamp(24px, 6vw, 38px);
  font-size: 0;
  cursor: pointer;
}
.btn-slider svg {
  width: clamp(24px, 6vw, 38px);
  height: clamp(24px, 6vw, 38px);
}
@media (hover: hover) {
  .btn-slider--first:hover path {
    fill: var(--c-first);
  }
  .btn-slider--first:hover rect {
    stroke: var(--c-first);
  }
}
.btn-slider--second path {
  fill: var(--c-fifth);
}
.btn-slider--second rect {
  stroke: var(--c-fifth);
}
@media (hover: hover) {
  .btn-slider--second:hover path {
    fill: var(--c-second);
  }
  .btn-slider--second:hover rect {
    stroke: var(--c-second);
  }
}
.btn--first {
  border-radius: var(--b-radius-third);
  color: var(--c-white);
  background: var(--c-third);
}
@media (hover: hover) {
  .btn--first:hover {
    background: var(--c-hover-second);
  }
}
.btn--second {
  padding: 12px 16px;
  border: 1px solid var(--c-white);
  display: block;
  width: max-content;
  border-radius: var(--b-radius-third);
}
@media (hover: hover) {
  .btn--second:hover {
    color: var(--c-fifth);
    border-color: var(--c-fifth);
  }
}
.btn--dropdown {
  display: flex;
  align-items: center;
  gap: 8px;
}
.btn--dropdown i {
  width: 10px;
  height: 10px;
  background: center/contain no-repeat url("../img/icons/arrow--dropdown.svg");
  transition: var(--anim2);
}

.swiper-pagination {
  display: none;
  align-items: center;
  justify-content: center;
  margin-top: 24px;
  gap: 16px;
}
.swiper-pagination-bullet {
  border-radius: 100%;
  width: 16px;
  height: 16px;
  background: var(--c-fourth);
}
.swiper-pagination-bullet-active {
  background: var(--c-white);
}
@media screen and (max-width: 820px) {
  .swiper-pagination {
    display: flex;
  }
}

.container {
  margin: 0 auto;
  padding: clamp(16px, 4vw, 40px);
  max-width: 1760px;
}

header {
  flex: 0 0 auto;
}

.wrapper {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

html {
  overflow-x: hidden;
  margin-right: calc(-1 * (100vw - 100% - 1px));
  padding: clamp(16px, 4vw, 32px);
  height: 100%;
  background: var(--bg-main);
}

body {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  height: 100%;
  gap: 24px;
}

main {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.header {
  position: fixed;
  left: clamp(16px, 4vw, 32px);
  right: clamp(16px, 4vw, 32px);
  top: clamp(16px, 4vw, 32px);
  z-index: 100;
  width: calc(100vw - clamp(16px, 4vw, 32px) * 2);
  transition: top var(--anim2);
}
.header--offset {
  top: 0;
}
.header .container {
  position: relative;
  border-radius: var(--b-radius-first);
  padding: clamp(16px, 2vw, 24px);
  padding-bottom: 16px;
  background: var(--bg-second);
}
.header nav {
  margin-right: auto;
}
@media (hover: hover) {
  .header nav a:hover {
    font-weight: 700;
  }
}
@media screen and (max-width: 1600px) {
  .header nav {
    position: absolute;
    left: 0;
    top: calc(100% + var(--anim-offset));
    border-radius: var(--b-radius-first);
    padding: 0 10px;
    width: 300px;
    background: var(--bg-second);
    opacity: 0;
    visibility: hidden;
    transition: var(--anim2);
  }
}
@media screen and (max-width: 820px) {
  .header nav {
    position: relative;
    top: 0;
    width: 100%;
    opacity: unset;
    visibility: unset;
  }
}
.header__top {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--c-second);
  padding-bottom: 16px;
  gap: 20px;
}
@media screen and (max-width: 820px) {
  .header__top {
    border-bottom: unset;
    padding-bottom: unset;
  }
}
.header__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 13px;
  gap: 16px;
}
.header__bottom-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  gap: 16px;
}
@media screen and (max-width: 820px) {
  .header__bottom-wrap {
    flex-direction: column;
    justify-content: unset;
    overflow: auto;
    width: 100%;
    min-height: calc(100% - var(--offset-header) + clamp(16px, 4vw, 32px) * 2 - 32px);
  }
}
@media screen and (max-width: 820px) {
  .header__bottom .btn {
    width: 100%;
    text-align: center;
  }
}
@media screen and (min-width: 769px) {
  .header__bottom.open nav {
    top: 100%;
    opacity: 1;
    visibility: visible;
  }
}
@media screen and (max-width: 820px) {
  .header__bottom {
    position: fixed;
    left: 0;
    right: 0;
    top: var(--anim-offset);
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: unset;
    padding: calc(var(--offset-header) + clamp(16px, 4vw, 32px) * 2) 32px 32px;
    background: var(--bg-second);
    opacity: 0;
    visibility: hidden;
    transition: var(--anim2);
    gap: 24px;
  }
  .header__bottom.open {
    top: 0;
    opacity: 1;
    visibility: visible;
  }
}
.header__logo {
  width: min(165px, 100%);
  font-size: 0;
}
@media screen and (max-width: 820px) {
  .header__logo {
    width: clamp(100px, 23vw, 165px);
  }
}
.header__group {
  display: flex;
  width: 500px;
  gap: clamp(24px, 4vw, 48px);
}
.header__group--right {
  justify-content: end;
}
.header__group--mob {
  display: none;
}
@media screen and (max-width: 820px) {
  .header__group--mob {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 100%;
    gap: 16px;
  }
}
@media screen and (max-width: 1280px) {
  .header__group {
    width: clamp(200px, 30vw, 290px);
  }
}
@media screen and (max-width: 820px) {
  .header__group {
    display: none;
  }
}
.header__opening-hours {
  display: flex;
  align-items: center;
  gap: 5px;
}
.header__opening-hours p {
  margin: unset;
  white-space: nowrap;
}
@media screen and (max-width: 1280px) {
  .header__opening-hours {
    flex-direction: column;
    align-items: flex-start;
  }
}
.header__place, .header__phones {
  display: flex;
  align-items: center;
  gap: 8px;
}
.header__place img, .header__phones img {
  width: 24px;
  height: 24px;
}
.header__place.header__place--mob {
  display: flex;
}
@media screen and (max-width: 1600px) {
  .header__place {
    display: none;
  }
}
.header__phones {
  position: relative;
}
.header__phones > span {
  cursor: pointer;
}
.header__phones ul {
  position: absolute;
  right: 0;
  top: calc(100% + var(--anim-offset));
  z-index: 5;
  border-radius: var(--b-radius-second);
  box-shadow: var(--shadow-first);
  background: var(--bg-first);
  opacity: 0;
  visibility: hidden;
  transition: var(--anim2);
}
.header__phones a {
  display: block;
  padding: 6px 16px;
}
@media (hover: hover) {
  .header__phones a:hover {
    background: var(--c-hover-third);
  }
}
.header__phones.header__phones--mob {
  display: flex;
  flex-direction: column;
}
.header__phones.header__phones--mob a {
  display: flex;
  align-items: center;
  padding: unset;
  gap: 8px;
}
.header__phones.open i {
  transform: rotate(180deg) translateY(-1px);
}
.header__phones.open ul {
  top: 100%;
  opacity: 1;
  visibility: visible;
}
.header__menu {
  display: flex;
  margin-right: auto;
}
.header__menu > li {
  padding: 13px 17px;
}
.header__menu-btn {
  --line-offset: 2px;
  position: relative;
  z-index: 15;
  display: none;
  flex-shrink: 0;
  width: 20px;
  height: 14px;
  cursor: pointer;
}
.header__menu-btn span {
  position: absolute;
  display: block;
  border-radius: 10px;
  width: 100%;
  height: 1px;
